'use client'

import { useState } from 'react'
import Image from 'next/image'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Label } from '@/components/ui/label'
import { Checkbox } from '@/components/ui/checkbox'
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { Upload, CheckCircle, Info, Calculator, Clock, Award } from 'lucide-react'

export default function CustomOrderPage() {
  const [currentStep, setCurrentStep] = useState(1)
  const [formData, setFormData] = useState({
    // Step 1: Basic Info
    name: '',
    email: '',
    phone: '',
    
    // Step 2: Product Details
    productType: '',
    style: '',
    dimensions: {
      length: '',
      width: '',
      height: ''
    },
    materials: [] as string[],
    colors: '',
    
    // Step 3: Additional Features
    features: [] as string[],
    budget: '',
    timeline: '',
    
    // Step 4: Details & Files
    description: '',
    inspiration: '',
    files: [] as File[]
  })
  const [isSubmitted, setIsSubmitted] = useState(false)

  const steps = [
    { number: 1, title: 'Osnovne informacije', description: 'Vaši kontakt podaci' },
    { number: 2, title: 'Detalji proizvoda', description: 'Tip, stil i dimenzije' },
    { number: 3, title: 'Dodatne opcije', description: 'Funkcije i preferencije' },
    { number: 4, title: 'Finalizacija', description: 'Opis i dokumenti' }
  ]

  const productTypes = [
    { value: 'bed', label: 'Krevet', image: 'https://images.pexels.com/photos/1743229/pexels-photo-1743229.jpeg' },
    { value: 'wardrobe', label: 'Ormar', image: 'https://images.pexels.com/photos/1743227/pexels-photo-1743227.jpeg' },
    { value: 'nightstand', label: 'Noćni ormarić', image: 'https://images.pexels.com/photos/1743228/pexels-photo-1743228.jpeg' },
    { value: 'dresser', label: 'Komoda', image: 'https://images.pexels.com/photos/1743231/pexels-photo-1743231.jpeg' },
    { value: 'desk', label: 'Radni sto', image: 'https://images.pexels.com/photos/1743229/pexels-photo-1743229.jpeg' },
    { value: 'other', label: 'Ostalo', image: 'https://images.pexels.com/photos/1743227/pexels-photo-1743227.jpeg' }
  ]

  const styles = [
    'Moderni',
    'Klasični',
    'Minimalistički',
    'Rustični',
    'Industrijski',
    'Skandinavski',
    'Art Deco',
    'Ostalo'
  ]

  const materials = [
    'Masivno drvo hrasta',
    'Masivno drvo bukve',
    'Masivno drvo oraha',
    'Laminat',
    'MDF',
    'Prava koža',
    'Eko koža',
    'Tkanina',
    'Metal',
    'Staklo'
  ]

  const features = [
    'LED osvjetljenje',
    'USB punjači',
    'Bežični punjač',
    'Skriveni pretinci',
    'Soft-close mehanizam',
    'Automatsko otvaranje',
    'Ogledalo',
    'Dodatni ormarići',
    'Masaža funkcija',
    'Grijanje'
  ]

  const budgetRanges = [
    'Do 1.000 KM',
    '1.000 - 2.000 KM',
    '2.000 - 3.000 KM',
    '3.000 - 5.000 KM',
    'Preko 5.000 KM',
    'Bez ograničenja'
  ]

  const timelineOptions = [
    'Što prije',
    '1-2 mjeseca',
    '2-3 mjeseca',
    '3-6 mjeseci',
    'Preko 6 mjeseci',
    'Fleksibilno'
  ]

  const handleNext = () => {
    if (currentStep < 4) {
      setCurrentStep(currentStep + 1)
    }
  }

  const handlePrevious = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1)
    }
  }

  const handleSubmit = () => {
    console.log('Custom order submitted:', formData)
    setIsSubmitted(true)
  }

  const updateFormData = (field: string, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))
  }

  const updateDimensions = (dimension: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      dimensions: {
        ...prev.dimensions,
        [dimension]: value
      }
    }))
  }

  const toggleMaterial = (material: string) => {
    setFormData(prev => ({
      ...prev,
      materials: prev.materials.includes(material)
        ? prev.materials.filter(m => m !== material)
        : [...prev.materials, material]
    }))
  }

  const toggleFeature = (feature: string) => {
    setFormData(prev => ({
      ...prev,
      features: prev.features.includes(feature)
        ? prev.features.filter(f => f !== feature)
        : [...prev.features, feature]
    }))
  }

  if (isSubmitted) {
    return (
      <div className="min-h-screen py-8">
        <div className="container mx-auto px-4">
          <div className="max-w-2xl mx-auto text-center py-16">
            <CheckCircle className="h-24 w-24 text-green-500 mx-auto mb-6" />
            <h1 className="text-4xl font-bold mb-4" style={{ fontFamily: 'Playfair Display, serif' }}>
              Zahtjev je uspješno poslan!
            </h1>
            <p className="text-xl text-muted-foreground mb-8">
              Hvala vam na povjerenju. Naš tim će analizirati vaš zahtjev i kontaktirati vas u roku od 24 sata sa detaljnom ponudom.
            </p>
            <div className="bg-muted/30 p-6 rounded-lg mb-8">
              <h3 className="font-semibold mb-4">Šta slijedi:</h3>
              <div className="space-y-3 text-left">
                <div className="flex items-center space-x-3">
                  <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-white text-sm">1</div>
                  <span>Analiza vašeg zahtjeva (24h)</span>
                </div>
                <div className="flex items-center space-x-3">
                  <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-white text-sm">2</div>
                  <span>Kreiranje dizajna i ponude (2-3 dana)</span>
                </div>
                <div className="flex items-center space-x-3">
                  <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-white text-sm">3</div>
                  <span>Prezentacija rješenja (video poziv ili lični susret)</span>
                </div>
                <div className="flex items-center space-x-3">
                  <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-white text-sm">4</div>
                  <span>Finalizacija detalja i početak proizvodnje</span>
                </div>
              </div>
            </div>
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Button size="lg" onClick={() => {
                setIsSubmitted(false)
                setCurrentStep(1)
                setFormData({
                  name: '', email: '', phone: '', productType: '', style: '',
                  dimensions: { length: '', width: '', height: '' },
                  materials: [], colors: '', features: [], budget: '', timeline: '',
                  description: '', inspiration: '', files: []
                })
              }}>
                Novi zahtjev
              </Button>
              <Button size="lg" variant="outline">
                Povratak na početnu
              </Button>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen py-8">
      <div className="container mx-auto px-4">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold mb-4" style={{ fontFamily: 'Playfair Display, serif' }}>
            Naručite po mjeri
          </h1>
          <p className="text-xl text-muted-foreground max-w-3xl mx-auto">
            Kreirajte jedinstveni namještaj prilagođen vašim potrebama i prostoru. 
            Naš tim će vam pomoći da ostvarite vašu viziju.
          </p>
        </div>

        {/* Benefits */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12">
          <Card className="text-center p-6">
            <Award className="h-8 w-8 text-primary mx-auto mb-3" />
            <h3 className="font-semibold mb-2">Jedinstveni dizajn</h3>
            <p className="text-sm text-muted-foreground">Namještaj kreiran posebno za vas</p>
          </Card>
          <Card className="text-center p-6">
            <Calculator className="h-8 w-8 text-primary mx-auto mb-3" />
            <h3 className="font-semibold mb-2">Precizne dimenzije</h3>
            <p className="text-sm text-muted-foreground">Savršeno uklapanje u vaš prostor</p>
          </Card>
          <Card className="text-center p-6">
            <Clock className="h-8 w-8 text-primary mx-auto mb-3" />
            <h3 className="font-semibold mb-2">Brza realizacija</h3>
            <p className="text-sm text-muted-foreground">Od ideje do gotovog proizvoda</p>
          </Card>
        </div>

        <div className="max-w-4xl mx-auto">
          {/* Progress Steps */}
          <div className="mb-8">
            <div className="flex items-center justify-between">
              {steps.map((step, index) => (
                <div key={step.number} className="flex items-center">
                  <div className={`flex items-center ${index < steps.length - 1 ? 'flex-1' : ''}`}>
                    <div className={`w-10 h-10 rounded-full flex items-center justify-center text-sm font-medium ${
                      currentStep >= step.number 
                        ? 'bg-primary text-primary-foreground' 
                        : 'bg-muted text-muted-foreground'
                    }`}>
                      {step.number}
                    </div>
                    <div className="ml-3 hidden md:block">
                      <div className={`text-sm font-medium ${
                        currentStep >= step.number ? 'text-foreground' : 'text-muted-foreground'
                      }`}>
                        {step.title}
                      </div>
                      <div className="text-xs text-muted-foreground">
                        {step.description}
                      </div>
                    </div>
                  </div>
                  {index < steps.length - 1 && (
                    <div className={`flex-1 h-0.5 mx-4 ${
                      currentStep > step.number ? 'bg-primary' : 'bg-muted'
                    }`} />
                  )}
                </div>
              ))}
            </div>
          </div>

          {/* Form Content */}
          <Card>
            <CardHeader>
              <CardTitle>
                {steps[currentStep - 1].title}
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              {/* Step 1: Basic Info */}
              {currentStep === 1 && (
                <div className="space-y-4">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                      <Label htmlFor="name">Ime i prezime *</Label>
                      <Input
                        id="name"
                        value={formData.name}
                        onChange={(e) => updateFormData('name', e.target.value)}
                        placeholder="Vaše ime i prezime"
                        required
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="email">Email adresa *</Label>
                      <Input
                        id="email"
                        type="email"
                        value={formData.email}
                        onChange={(e) => updateFormData('email', e.target.value)}
                        placeholder="vasa@email.com"
                        required
                      />
                    </div>
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="phone">Broj telefona *</Label>
                    <Input
                      id="phone"
                      value={formData.phone}
                      onChange={(e) => updateFormData('phone', e.target.value)}
                      placeholder="+387 XX XXX XXX"
                      required
                    />
                  </div>
                </div>
              )}

              {/* Step 2: Product Details */}
              {currentStep === 2 && (
                <div className="space-y-6">
                  <div>
                    <Label className="text-base font-medium mb-4 block">Tip proizvoda *</Label>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                      {productTypes.map((type) => (
                        <div
                          key={type.value}
                          className={`relative cursor-pointer rounded-lg border-2 transition-colors ${
                            formData.productType === type.value
                              ? 'border-primary bg-primary/5'
                              : 'border-muted hover:border-primary/50'
                          }`}
                          onClick={() => updateFormData('productType', type.value)}
                        >
                          <div className="aspect-square relative">
                            <Image
                              src={type.image}
                              alt={type.label}
                              fill
                              className="object-cover rounded-t-lg"
                            />
                          </div>
                          <div className="p-3 text-center">
                            <span className="text-sm font-medium">{type.label}</span>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label>Stil *</Label>
                    <Select value={formData.style} onValueChange={(value) => updateFormData('style', value)}>
                      <SelectTrigger>
                        <SelectValue placeholder="Odaberite stil" />
                      </SelectTrigger>
                      <SelectContent>
                        {styles.map((style) => (
                          <SelectItem key={style} value={style}>
                            {style}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <Label className="text-base font-medium mb-4 block">Dimenzije (cm)</Label>
                    <div className="grid grid-cols-3 gap-4">
                      <div className="space-y-2">
                        <Label htmlFor="length">Dužina</Label>
                        <Input
                          id="length"
                          value={formData.dimensions.length}
                          onChange={(e) => updateDimensions('length', e.target.value)}
                          placeholder="200"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="width">Širina</Label>
                        <Input
                          id="width"
                          value={formData.dimensions.width}
                          onChange={(e) => updateDimensions('width', e.target.value)}
                          placeholder="160"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="height">Visina</Label>
                        <Input
                          id="height"
                          value={formData.dimensions.height}
                          onChange={(e) => updateDimensions('height', e.target.value)}
                          placeholder="100"
                        />
                      </div>
                    </div>
                  </div>

                  <div>
                    <Label className="text-base font-medium mb-4 block">Materijali</Label>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
                      {materials.map((material) => (
                        <div key={material} className="flex items-center space-x-2">
                          <Checkbox
                            id={material}
                            checked={formData.materials.includes(material)}
                            onCheckedChange={() => toggleMaterial(material)}
                          />
                          <Label htmlFor={material} className="text-sm">
                            {material}
                          </Label>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="colors">Boje i završne obrade</Label>
                    <Input
                      id="colors"
                      value={formData.colors}
                      onChange={(e) => updateFormData('colors', e.target.value)}
                      placeholder="Opišite željene boje i završne obrade"
                    />
                  </div>
                </div>
              )}

              {/* Step 3: Additional Features */}
              {currentStep === 3 && (
                <div className="space-y-6">
                  <div>
                    <Label className="text-base font-medium mb-4 block">Dodatne funkcije</Label>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
                      {features.map((feature) => (
                        <div key={feature} className="flex items-center space-x-2">
                          <Checkbox
                            id={feature}
                            checked={formData.features.includes(feature)}
                            onCheckedChange={() => toggleFeature(feature)}
                          />
                          <Label htmlFor={feature} className="text-sm">
                            {feature}
                          </Label>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="space-y-2">
                    <Label>Budžet *</Label>
                    <RadioGroup
                      value={formData.budget}
                      onValueChange={(value) => updateFormData('budget', value)}
                    >
                      {budgetRanges.map((range) => (
                        <div key={range} className="flex items-center space-x-2">
                          <RadioGroupItem value={range} id={range} />
                          <Label htmlFor={range}>{range}</Label>
                        </div>
                      ))}
                    </RadioGroup>
                  </div>

                  <div className="space-y-2">
                    <Label>Željeni rok izrade *</Label>
                    <RadioGroup
                      value={formData.timeline}
                      onValueChange={(value) => updateFormData('timeline', value)}
                    >
                      {timelineOptions.map((option) => (
                        <div key={option} className="flex items-center space-x-2">
                          <RadioGroupItem value={option} id={option} />
                          <Label htmlFor={option}>{option}</Label>
                        </div>
                      ))}
                    </RadioGroup>
                  </div>
                </div>
              )}

              {/* Step 4: Details & Files */}
              {currentStep === 4 && (
                <div className="space-y-6">
                  <div className="space-y-2">
                    <Label htmlFor="description">Detaljni opis *</Label>
                    <Textarea
                      id="description"
                      value={formData.description}
                      onChange={(e) => updateFormData('description', e.target.value)}
                      placeholder="Opišite detaljno vaše potrebe, funkcionalnosti koje želite, specifične zahtjeve..."
                      rows={6}
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="inspiration">Inspiracija i reference</Label>
                    <Textarea
                      id="inspiration"
                      value={formData.inspiration}
                      onChange={(e) => updateFormData('inspiration', e.target.value)}
                      placeholder="Opišite stil koji vam se sviđa, spomenite brendove, linkove ili bilo što što može pomoći..."
                      rows={4}
                    />
                  </div>

                  <div className="space-y-2">
                    <Label>Dodajte slike ili dokumente</Label>
                    <div className="border-2 border-dashed border-muted-foreground/25 rounded-lg p-8 text-center">
                      <Upload className="h-12 w-12 text-muted-foreground mx-auto mb-4" />
                      <p className="text-muted-foreground mb-2">
                        Povucite i otpustite datoteke ovdje ili kliknite za odabir
                      </p>
                      <p className="text-sm text-muted-foreground">
                        Podržani formati: JPG, PNG, PDF (max 10MB po datoteci)
                      </p>
                      <Button variant="outline" className="mt-4">
                        Odaberite datoteke
                      </Button>
                    </div>
                  </div>

                  <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
                    <div className="flex items-start space-x-3">
                      <Info className="h-5 w-5 text-blue-500 mt-0.5" />
                      <div className="text-sm">
                        <p className="font-medium text-blue-900 mb-1">Korisni savjeti:</p>
                        <ul className="text-blue-700 space-y-1">
                          <li>• Dodajte slike prostora gdje će se namještaj nalaziti</li>
                          <li>• Priložite skice ili crteže ako ih imate</li>
                          <li>• Spomenite sve specifične zahtjeve ili ograničenja</li>
                          <li>• Uključite reference stilova koji vam se sviđaju</li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              )}

              {/* Navigation Buttons */}
              <Separator />
              <div className="flex justify-between">
                <Button
                  variant="outline"
                  onClick={handlePrevious}
                  disabled={currentStep === 1}
                >
                  Prethodno
                </Button>
                
                {currentStep < 4 ? (
                  <Button onClick={handleNext}>
                    Sljedeće
                  </Button>
                ) : (
                  <Button onClick={handleSubmit}>
                    Pošaljite zahtjev
                  </Button>
                )}
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
